import * as THREE from "three";
import React, { useMemo, useRef } from "react";
import {
  Geometry,
  Base,
  Subtraction,
  Addition,
  CSGGeometryRef,
} from "@react-three/csg";

const box = new THREE.BoxGeometry();
// const cyl = new THREE.CylinderGeometry(1, 1, 2, 20);
const tri = new THREE.CylinderGeometry(1, 1, 2, 3);

export type Point = [number, number];

const createShape = (edges: Point[]) =>
  edges
    .reduce(
      (shape, [x, y], k) => shape[k > 0 ? "lineTo" : "moveTo"](x, y),
      new THREE.Shape(),
    )
    .closePath();

export const Rims = function ({ wireframe, meshRef }) {
  const csg = useRef<CSGGeometryRef>(null);

  const shape = useMemo(() => {
    const d = 6,
      slit = 0;
    const points: Point[] = [
      [5 - slit, 0 + slit],
      [5 - slit, d - slit],
      [d / 2 - slit, 10 - slit],
      [d / 2 - slit, 20 - slit],
    ];
    return new THREE.ExtrudeGeometry(
      createShape(
        points.concat([...points].reverse().map(([x, y]) => [-x, y])),
      ),
      { depth: 1, bevelEnabled: false },
    );
  }, []);

  const hole = useMemo(() => {
    const d = 6,
      slit = 1.5 - 0.2;
    const points: Point[] = [
      [5 - slit, 0 + slit],
      [5 - slit, d - slit],
      [d / 2 - slit, 10 - slit],
      [d / 2 - slit, 20 - slit],
    ];
    return new THREE.ExtrudeGeometry(
      createShape(
        points.concat([...points].reverse().map(([x, y]) => [-x, y])),
      ),
      { depth: 1, bevelEnabled: false },
    );
  }, []);

  const peg = useMemo(() => {
    const d = 6,
      slit = 1.5;
    const points: Point[] = [
      [5 - slit, 0 + slit],
      [5 - slit, d - slit],
      [d / 2 - slit, 10 - slit],
      [d / 2 - slit, 20 - slit],
    ];
    return new THREE.ExtrudeGeometry(
      createShape(
        points.concat([...points].reverse().map(([x, y]) => [-x, y])),
      ),
      { depth: 1, bevelEnabled: false },
    );
  }, []);

  const rim = useMemo(() => {
    const points: Point[] = [
      [0, 0],
      [10 + 2 + 1, 0],
      [15 + 2 + 1, 15 + 1],
      [15 + 2 + 1, 15 + 1 + 1],
      [15 + 2 + 1 - 3, 15 + 1 + 1],
      [15 + 2 + 1 - 3, 15 + 1 + 0.5],
      [15 + 2, 15 + 1],
      [10 + 2, 1],
      [2, 1],
      [2, 20],
      [0, 20],
    ];
    return new THREE.ExtrudeGeometry(createShape(points), {
      depth: 1,
      bevelEnabled: false,
    });
  }, []);

  const dot = useMemo(() => {
    const d = 4,
      slit = 0.1;
    const points: Point[] = [
      [d / 2 + slit, 1],
      [d / 2 + slit, -10],
      [0, -10 - 2],
      [-(d / 2 + slit), -10],
      [-(d / 2 + slit), 1],
    ];
    return new THREE.ExtrudeGeometry(createShape(points), {
      depth: 1,
      bevelEnabled: false,
    });
  }, []);

  return (
    <mesh ref={meshRef} receiveShadow castShadow>
      <Geometry ref={csg} computeVertexNormals>
        <Base name="base" geometry={shape} scale={[1, 1, 80]} />
        <Addition
          name="peg"
          geometry={peg}
          position={[0, 0, 80]}
          scale={[1, 1, 38]}
        />
        <Addition
          name="rim"
          geometry={rim}
          position={[-10, 0, 0]}
          rotation={[0, Math.PI / 2, 0]}
          scale={[1, 1, 20]}
        />
        <Subtraction
          name="dot"
          geometry={dot}
          position={[0, 15 + 1, -20]}
          rotation={[0, 0, 0]}
          scale={[1, 1, 10]}
        />
        <Base
          name="base"
          geometry={shape}
          position={[50, 0, 0]}
          scale={[1, 1, 78]}
        />
        <Subtraction
          name="hole"
          geometry={hole}
          position={[50, 0, 38]}
          scale={[1, 1, 40]}
        />
        <Addition
          name="rim"
          geometry={rim}
          position={[-10 + 50, 0, 0]}
          rotation={[0, Math.PI / 2, 0]}
          scale={[1, 1, 20]}
        />
        <Subtraction
          name="dot"
          geometry={dot}
          position={[50, 15 + 1, -20]}
          rotation={[0, 0, 0]}
          scale={[1, 1, 10]}
        />
        {/* <Base name="base" geometry={box} scale={[30, 30, 30]} /> */}
        {/* <Subtraction name="cavity" geometry={box} scale={[27, 27, 27]} />
        <Addition
          name="roof"
          geometry={tri}
          scale={[25, 15, 14.25]}
          rotation={[-Math.PI / 2, 0, 0]}
          position={[0, 22, 0]}
        /> */}
      </Geometry>
      {wireframe ? (
        <meshBasicMaterial color="#2f7f4f" wireframe />
      ) : (
        <meshStandardMaterial color="#9d4b4b" />
      )}
    </mesh>
  );
};
